.image{
    animation-name: image;
    position: relative;
    animation-duration: 5s;
    animation-delay: 1s;
    animation-timing-function: linear;
    animation-iteration-count:infinite;
    animation-direction: alternate;
}

@keyframes image{
    0% {
        left: 0px;
        top: 0px;
    }
    25% {
        left: 15px;
        top: 15px;
    }
    50% {
        left: 20px;
        top: 0px;
    }
    75% {
        left: 0px;
        top: 20px;
    }
    100% {
        left: 0px;
        top: 0px;
    }
    from{
        background: linear-gradient(-45deg,#6bc5f8,#cf59e6,#6bc5f8,#cf59e6);
    }
    to{
        background: linear-gradient(-45deg,lightblue,#7928CA,lightblue,#7928CA);  
    }
}